@import './reset.css';
@import url('https://fonts.googleapis.com/css2?family=DM+Sans:wght@400;500;700&family=Poppins:wght@400;500;700&display=swap');

:root {
	/* Colors */
	--blackpure: #000;
	--black: #171718;
	--black-border: #26292d;
	--blue-border: #2196f3;
	--white: #fff;
	/* --purple: #5c62ec; */
	--gradient: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);

	/* Nav */
	--nav-bg: var(--gradient);
	--nav-border: var(--black-border);
	--nav-text: var(--white);

	/* Header */
	--header-bg: var(--white);
	--header-text: var(--black);

	/* Footer */
	--footer-bg: var(--gradient);
	--footer-text: var(--white);

	/* Light mode */
	--page-bg: var(--white);
	--text-color: var(--black);
	--accent: var(--blue-border);
	/* --accent: var(--purple); */
	--title-1: var(--accent);

	--project-card-bg: var(--white);
	--project-card-text: var(--black);
	--box-shadow: 0px 5px 35px rgba(0, 0, 0, 0.25);
}

.dark {
	/* Dark mode */
	--page-bg: #252526;
	--text-color: var(--white);

	--title-1: var(--white);

	/* Project */
	--project-card-bg: var(--black);
	--project-card-text: var(--white);
	--box-shadow: 0px 5px 35px rgba(0, 0, 0, 0.8);

	/* Footer */
	--footer-bg: var(--black);
	--footer-text: var(--white);

	/* Nav */
	--nav-bg: var(--black);
	--nav-border: var(--blue-border);
	/* Header */
	--header-bg: var(--black);
	--header-text: var(--white);
}

html,
body {
	min-height: 100vh;

	font-family: 'DM Sans', sans-serif;
	letter-spacing: -0.5px;

	/* Light mode */
	background-color: var(--page-bg);
	color: var(--text-color);
}
#root {
	min-height: 100vh;
}
.App {
	min-height: 100vh;
	display: flex;
	flex-direction: column;
}
/* General */

.container {
	margin: 0 auto;
	padding: 0 15px;
	max-width: 1200px;
}

.none {
	display: none !important;
}

.section {
	padding: 70px 0;
}

.title-1 {
	margin-bottom: 60px;

	font-size: 60px;
	font-weight: 700;
	line-height: 1.3;
	color: var(--title-1);
	text-align: center;
}

.title-2 {
	margin-bottom: 20px;

	font-size: 40px;
	font-weight: 700;
	line-height: 1.3;
}




/* Btn */

.btn {
	display: inline-block;
	height: 48px;
	padding: 12px 28px;

	border-radius: 5px;
	
	background: linear-gradient(90deg, #3f51b5, transparent) #2196f3;
	/* text-decoration: none; */
	transition: background-color 1s;

	color: var(--white);
	letter-spacing: 0.15px;
	font-size: 16px;
	font-weight: 500;
}

.btn:hover {
	background-color: #e91e63;
	color: #fff;
}

.btn:active {
	position: relative;
	top: 2px;
}

/* Btn outline */

.btn-outline {
	display: flex;
	column-gap: 10px;
	align-items: center;

	height: 48px;
	padding: 12px 20px;

	border-radius: 5px;
	border: 1px solid #000;
	background-color: #fff;
	color: #000;

	transition: opacity 0.2s ease-in;
}

.btn-outline:hover {
	opacity: 0.8;
}

.btn-outline:active {
	position: relative;
	top: 1px;
}

/* Projects  */

.projects {
	display: flex;
	justify-content: center;
	flex-wrap: wrap;
	column-gap: 30px;
	row-gap: 30px;
}



/* Project details */

.project-details {
	margin: 0 auto;
	max-width: 865px;

	display: flex;
	flex-direction: column;
	align-items: center;

	text-align: center;
}

.project-details__cover {
	max-width: 100%;
	margin-bottom: 40px;

	border-radius: 10px;
	/* box-shadow: var(--box-shadow); */
	box-shadow: 0px 5px 10px 0px rgba(0, 0, 0, 0.5);

	transition: transform ease .5s, box-shadow ease .5s;
}

.project-details__desc {
	margin-bottom: 30px;

	font-weight: 700;
	font-size: 24px;
	line-height: 1.3;
}

/* Content list */

.content-list {
	margin: 0 auto;
	max-width: 570px;
	display: flex;
	flex-direction: column;
	align-items: center;
	row-gap: 60px;
	text-align: center;
}

.content-list a {
	color: var(--accent);
}

.content-list__item {
	font-size: 24px;
	line-height: 1.5;
}

.content-list__item p + p {
	margin-top: 0.5em;
}
.mt{
	margin-top: 8em;
}

.project-details__cover:hover {
	transform: translateY(-5px);
	box-shadow: 0px 10px 20px 2px rgba(0, 0, 0, 0.25);
}

@media screen and (max-width: 768px) {
	.title-1 {
	  font-size: 36px;
	}
  
	.title-2 {
		margin-bottom: 15px;
	
		font-size: 30px;
		font-weight: 700;
		line-height: 1.1;
	}
	
	.section {
	  padding: 30px 0;
	}
  
	.btn {
	  height: 40px;
	  padding: 10px 20px;
	  font-size: 14px;
	}
  
  }